<template>
  <div>
    <el-table :data="orderlist" height="410" border>
      <el-table-column prop="to_id" label="ID" />
      <el-table-column prop="to_title" label="" />
      <el-table-column prop="to_name" label="" />
      <el-table-column prop="customer.u_nickname" label="" />
      <el-table-column prop="to_price" label="()" />
      <el-table-column prop="" label="">
        <template slot-scope="scope">
          <p>{{ scope.row.to_status === 1 ? "" : scope.row.to_status === 2 ? "" : "" }}</p>
        </template>
      </el-table-column>
      <el-table-column prop="to_createTime" label="" width="100" />
      <el-table-column label="">
        <div slot-scope="scope">
          <el-button title="" type="primary" size="mini" icon="iconfont icon-dingdan" @click="order(scope.row)" />
        </div>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible1" width="40%" :before-close="handleClose1">
      <div>
        <el-form :model="serverform" label-width="70">
          <el-form-item label="()">
            <el-select v-model="serverform.to_status">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="2"></el-option>
              <el-option label="" :value="3"></el-option>
            </el-select>
            <el-input v-model="serverform.to_status"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose1"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dialogVisible1: false,
      dialogVisible2: false,
      serverform: {},
      orderlist: [],
      id: JSON.parse(window.sessionStorage.user).u_id
    }
  },
  created() {
    this.getorderlist()
  },
  methods: {
    // 
    async getorderlist() {
      const { data: res } = await this.$http.get("goods/showOrderByShopper", { params: { id: this.id } })
      console.log(res.obj)
      this.orderlist = res.obj
    },
    // 
    async submit() {
      const { data: res } = await this.$http.get("goods/updateOrderByShopper")
    },
    // 
    order(row) {
      this.dialogVisible1 = true
      this.serverform.to_id = row.to_id
    },
    // 
    handleClose1() {
      this.dialogVisible1 = false
    },
    handleClose2() {
      this.dialogVisible2 = false
    }
  }
}
</script>
